import React, { useState, useEffect } from 'react';
import './TeamScreen.css';
import { useParams, useNavigate } from 'react-router-dom';
import defaultPic from '../../assets/default-profile-pic.png';
import Navbar from '../../components/Navbar/Navbar';
import MainButton from '../../components/Buttons/MainButton/MainButton';
import SecondaryButton from '../../components/Buttons/SecondaryButton/SecondaryButton';

// TeamScreen component to manage team members and managers
const TeamScreen = () => {
  const [team, setTeam] = useState([]);
  const [currentUser, setCurrentUser] = useState(null);
  const [expandedManagers, setExpandedManagers] = useState([]);
  const navigate = useNavigate();
  // extract domain and username from the URL parameters
  const { domain, username } = useParams();


  // Fetch user data from the server
  useEffect(() => {
      const fetchUser = async () => {
          try {
              const res = await fetch(`/api/organizations/${domain}/users/${username}`, {
              method: 'GET',
              headers: {
                  'Content-Type': 'application/json',
                  Authorization: `Bearer ${localStorage.getItem('token')}`,
              },
            });
            const data = await res.json();
            setCurrentUser(data);
            console.log('Fetched user:', data);
          } catch (error) {
            console.error('Error fetching user:', error);
        }
    };
    fetchUser();
  }, [domain, username]);


  // Fetch team data based on the current user's role
 useEffect(() => {
  const fetchTeam = async () => {
    const token = localStorage.getItem('token');
    if (!token || !currentUser) return;

    // Determine the endpoint based on the user's role
    const endpoint =
      currentUser.role === 'organization_head'
        ? `/api/organizations/${domain}/users`
        : `/api/organizations/${domain}/users/${username}/team`;

    try {
      const res = await fetch(endpoint, {
        method: 'GET',
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });

      if (!res.ok) {
        throw new Error(`Failed to fetch team: ${res.statusText}`);
      }

      const data = await res.json();
      console.log("Fetched team data:", data);
      setTeam(data);
    } catch (error) {
      console.error("Error fetching team:", error);
    }
  };

  fetchTeam();
}, [domain, username, currentUser]);


// Handle adding a new user based on the current user's role
  const handleAddUser = () => {
    if (!currentUser) return;
    navigate(`/PlanIt/${domain}/${username}/registration`);
  };

  // Handle viewing a user's profile
  const handleView = (username) => {
    navigate(`/PlanIt/${domain}/${username}/profile`);
  };

  // Handle editing a user's profile based on their role
  const handleEdit = (username) => {
    if (!currentUser) return;
  
    if (currentUser.role === 'organization_head') {
      navigate(`/PlanIt/${domain}/${currentUser.username}/${username}/edit-manager`, {
        state: { currentUser }
      });
      
    } else if (currentUser.role === 'manager') {
      navigate(`/PlanIt/${domain}/${currentUser.username}/${username}/edit`);
    }
  };

  // Handle deleting a user
  const handleDelete = async (member) => {
    const token = localStorage.getItem('token');
    try {
      const res = await fetch(`/api/organizations/${domain}/users/${member.username}`, {
        method: 'DELETE',
        headers: {
          Authorization: `Bearer ${token}`
        }
      });

      if (!res.ok) throw new Error('Failed to delete user');
      alert(`${member.firstName} ${member.lastName} has been deleted.`);
      window.location.reload(); 
    } catch (error) {
      console.error("Error deleting user:", error);
      alert("Failed to delete user.");
    }
  };


  // Handle sending a password reset email
  const handleSendResetEmail = async (email) => {
    const confirmSend = window.confirm(`Send password reset email to ${email}?`);
    if (!confirmSend) return;
  
    try {
      const res = await fetch('/api/send-reset-email', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email }),
      });
  
      let message = 'Unknown response';
  
      if (res.headers.get('content-type')?.includes('application/json')) {
        const data = await res.json();
        message = data.message;
      } else {
        message = await res.text(); 
      }
  
      if (!res.ok) {
        alert(message || 'Failed to send reset email.');
      } else {
        alert(message || `Reset email sent to ${email}`);
      }
    } catch (err) {
      console.error('Error sending reset email:', err);
      alert('Something went wrong. Please try again later.');
    }
  };
  
// Toggle manager's team visibility
  const toggleManager = (managerId) => {
    setExpandedManagers(prev =>
      prev.includes(managerId)
        ? prev.filter(id => id !== managerId)
        : [...prev, managerId]
    );
  };

  // Separate managers and team members
  const managers = team.filter(user => user.role === 'manager');
  const members = team.filter(user => user.role === 'team_member');

  // Render a row for each user with action buttons
  const renderUserRow = (user, isIndented = false, isManager = false) => (
    <tr key={user._id} className={isIndented ? 'indented-row' : ''}>
      <td>
        <div className="member-info">
          <img src={user.profilePic || defaultPic} alt="Profile" className="member-pic" />
          <div>
            <strong>{user.firstName} {user.lastName}</strong><br />
            <span className="email-text">{user.email}</span>
          </div>
        </div>
      </td>
      <td>
        <div className="action-buttons">
          {isManager && (
            <button className="action-icon" title="Toggle team" onClick={() => toggleManager(user._id)}>
              <i className={`bi ${expandedManagers.includes(user._id) ? 'bi-chevron-up' : 'bi-chevron-down'}`}></i>
            </button>
          )}
          <button className="action-icon" title="View" onClick={() => handleView(user.username)}>
            <i className="bi bi-eye"></i>
          </button>
          <button className="action-icon" title="Edit" onClick={() => handleEdit(user.username)}>
            <i className="bi bi-pencil"></i>
          </button>
          <button className="action-icon" title="Delete" onClick={() => handleDelete(user)}>
            <i className="bi bi-trash"></i>
          </button>
          <button className="action-icon" title="Send password reset email" onClick={() => handleSendResetEmail(user.email)}>
            <i className="bi bi-envelope-arrow-up"></i>
          </button>
        </div>
      </td>
    </tr>
  );

  return (
    <div className="main-screen">
      <Navbar user={currentUser} domain={domain} />
      <div className="team-screen">
      <div className="team-header">
        {currentUser?.role === 'organization_head' ? (
          <h2>Managers and Their Teams</h2>
        ) : (
          <h2>Team Members</h2>
        )}
        {currentUser && (
          <SecondaryButton className="add-btn" onClick={handleAddUser} 
          title={currentUser.role === 'organization_head'
              ? '+ Add team manager'
              : '+ Add team member'}/>
        )}
      </div>

      {currentUser?.role === 'organization_head' ? (
        <p className="team-subtitle">
          Manage all managers in your organization and their assigned team members.<br />
          You can view, edit, and control account permissions for every user here.
        </p>
      ) : (
        <p className="team-subtitle">
          Manage your team members and their account permissions.<br />
          You can view their profiles, make changes, or remove members from your team.
        </p>
      )}

      <div className="table-section">
        <table className="team-table">
          <thead>
            <tr>
              <th>Name</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {currentUser?.role === 'organization_head' ? (
              managers.map(manager => (
                <React.Fragment key={manager._id}>
                  {renderUserRow(manager, false, true)}
                  {expandedManagers.includes(manager._id) &&
                    members
                      .filter(member => member.manager?.toString() === manager._id?.toString())
                      .map(member => renderUserRow(member, true))}
                </React.Fragment>
              ))
            ) : (
              team.map(member => renderUserRow(member))
            )}
          </tbody>
        </table>
      </div>
      </div>
    </div>
  );
};

export default TeamScreen;